// components/dynamic-zone/services/index.tsx
'use client';

import React, { useMemo, useRef } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import {
  motion,
  useReducedMotion,
  useMotionValue,
  useScroll,
  useSpring,
  useTransform,
  type Variants,
} from 'framer-motion';
import { Calendar, Gem, Scissors, Sparkles, User } from 'lucide-react';

import { Heading } from '@/components/elements/heading';
import { Subheading } from '@/components/elements/subheading';
import { Button } from '@/components/elements/button';

/** IMPORTANT: ne defaultolj localhostra prod miatt */
const STRAPI_BASE = (process.env.NEXT_PUBLIC_STRAPI_URL ?? process.env.NEXT_PUBLIC_API_URL ?? '')
  .replace(/\/$/, '')
  .replace(/\/api$/, '');

// ---------- Types ----------
type StrapiMedia = {
  data?: {
    attributes?: {
      url?: string;
      alternativeText?: string | null;
      width?: number | null;
      height?: number | null;
    } | null;
  } | null;

  url?: string;
  alternativeText?: string | null;
  attributes?: {
    url?: string;
    alternativeText?: string | null;
    width?: number | null;
    height?: number | null;
  } | null;
};

type DZItem = {
  __component: string;
  id: number;
  heading?: string | null;
  sub_heading?: string | null;
  cover?: StrapiMedia | null;
};

type PageLike = {
  id: number;
  documentId?: string;
  slug: string;
  label?: string | null;
  dynamic_zone?: DZItem[];
  order?: number;
  position?: number;
  sort?: number;
  locale?: string;
};

type ButtonTarget = '_self' | '_blank' | '_parent' | '_top' | string;

type ButtonItem = {
  id?: number;
  documentId?: string;
  text?: string | null;
  URL?: string | null;
  href?: string | null;
  variant?: string | null;
  target?: ButtonTarget | null;
  newTab?: boolean | null;
};

type CTAComponent = {
  id?: number;
  documentId?: string;
  heading?: string | null;
  sub_heading?: string | null;
  CTAs?: ButtonItem[] | null;
};

// ---------- Helpers ----------
function getHero(p: PageLike) {
  return p.dynamic_zone?.find((x) => x.__component === 'dynamic-zone.hero');
}

function getPageTitle(p: PageLike) {
  const label = p.label?.trim();
  if (label) return label;

  const hero = getHero(p);
  const t = hero?.heading?.trim();
  return t && t.length > 0 ? t : p.slug;
}

function getPageSubheading(p: PageLike): string | null {
  const hero = getHero(p);
  const s = hero?.sub_heading?.trim();
  return s && s.length > 0 ? s : null;
}

function getMediaUrl(m?: StrapiMedia | null) {
  const url = m?.data?.attributes?.url ?? m?.attributes?.url ?? m?.url ?? undefined;
  if (!url) return null;
  if (/^https?:\/\//i.test(url)) return url;
  return STRAPI_BASE ? `${STRAPI_BASE}${url.startsWith('/') ? '' : '/'}${url}` : url;
}

function getMediaAlt(m?: StrapiMedia | null, fallback = '') {
  return (
    m?.data?.attributes?.alternativeText ??
    m?.attributes?.alternativeText ??
    m?.alternativeText ??
    fallback
  ) || fallback;
}

function getPageCover(p: PageLike) {
  const hero: any = getHero(p);
  const cover: StrapiMedia | null | undefined = hero?.cover ?? null;
  const src = getMediaUrl(cover);
  if (!src) return null;
  return { src, alt: getMediaAlt(cover, getPageTitle(p)) };
}

function toHref(btn: ButtonItem) {
  return (btn.URL ?? btn.href ?? '#') as string;
}

function toTarget(btn: ButtonItem) {
  const target = btn.newTab ? '_blank' : (btn.target ?? undefined);
  const rel = target === '_blank' ? 'noopener noreferrer' : undefined;
  return { target, rel };
}

// clamp plugin nélkül is oké (line-clamp ha van, ha nincs: max-h + overflow-hidden)
const clamp3 = 'line-clamp-3 max-h-[3.9em] overflow-hidden';

const MotionDiv = motion.div;

// ---------- Cursor-follow shine (Framer Motion) ----------
function useCursorShine(enabled: boolean) {
  const mxRaw = useMotionValue('50%');
  const myRaw = useMotionValue('50%');
  const opRaw = useMotionValue(0);

  const mx = useSpring(mxRaw, { stiffness: 420, damping: 40, mass: 0.6 });
  const my = useSpring(myRaw, { stiffness: 420, damping: 40, mass: 0.6 });
  const op = useSpring(opRaw, { stiffness: 280, damping: 30, mass: 0.6 });

  const onMove = (e: React.MouseEvent<HTMLElement>) => {
    if (!enabled) return;
    const r = (e.currentTarget as HTMLElement).getBoundingClientRect();
    const x = ((e.clientX - r.left) / r.width) * 100;
    const y = ((e.clientY - r.top) / r.height) * 100;
    mxRaw.set(`${x.toFixed(2)}%`);
    myRaw.set(`${y.toFixed(2)}%`);
  };

  const onEnter = () => {
    if (!enabled) return;
    opRaw.set(1);
  };

  const onLeave = () => {
    if (!enabled) return;
    opRaw.set(0);
    mxRaw.set('50%');
    myRaw.set('50%');
  };

  return { mx, my, op, onMove, onEnter, onLeave };
}

function CursorShineLayer({
  mx,
  my,
  op,
  variant = 'light',
}: {
  mx: any;
  my: any;
  op: any;
  variant?: 'light' | 'dark';
}) {
  // light: fehér kártyákhoz, dark: featured/CTA sötét overlayre
  const muted = variant === 'dark' ? 'rgba(178,147,61,0.22)' : 'rgba(178,147,61,0.16)';
  const white = variant === 'dark' ? 'rgba(255,255,255,0.18)' : 'rgba(255,255,255,0.12)';

  return (
    <motion.div
      aria-hidden
      className="pointer-events-none absolute inset-0"
      style={
        {
          opacity: op,
          '--mx': mx,
          '--my': my,
        } as any
      }
    >
      <div
        className="absolute inset-0"
        style={{
          background: `radial-gradient(520px circle at var(--mx) var(--my), ${muted}, transparent 60%)`,
        }}
      />
      <div
        className="absolute inset-0"
        style={{
          background: `radial-gradient(420px circle at var(--mx) var(--my), ${white}, transparent 62%)`,
        }}
      />
    </motion.div>
  );
}

// ---------- Component ----------
export const Services = ({
  heading,
  sub_heading,
  pages,
  CTAs,
}: {
  heading?: string | null;
  sub_heading?: string | null;
  pages?: PageLike[] | null;
  CTAs?: CTAComponent | null;
}) => {
  const reduce = useReducedMotion();
  const shineEnabled = !reduce;

  // ====== Scroll based “ambient” background parallax ======
  const sectionRef = useRef<HTMLElement | null>(null);
  const { scrollYProgress } = useScroll({
    target: sectionRef,
    offset: ['start end', 'end start'],
  });

  const bgY1 = useSpring(useTransform(scrollYProgress, [0, 1], reduce ? [0, 0] : [-26, 26]), {
    stiffness: 120,
    damping: 26,
    mass: 0.7,
  });
  const bgY2 = useSpring(useTransform(scrollYProgress, [0, 1], reduce ? [0, 0] : [18, -18]), {
    stiffness: 120,
    damping: 26,
    mass: 0.7,
  });
  const bgO = useTransform(scrollYProgress, [0, 0.6, 1], [0.85, 1, 0.9]);

  // ====== Pages normalize + order ======
  const pageItems: PageLike[] = useMemo(() => {
    const raw: any = pages as any;
    const list: any[] = Array.isArray(raw) ? raw : (raw?.data ?? []);
    if (!Array.isArray(list) || list.length === 0) return [];

    const getOrder = (p: any): number | null => {
      const v =
        p?.order ??
        p?.position ??
        p?.sort ??
        p?.__order ??
        p?.__pivot?.order ??
        p?.__pivot?.position ??
        p?.pivot?.order ??
        p?.pivot?.position ??
        p?.meta?.order ??
        p?.attributes?.order ??
        p?.attributes?.position;
      return typeof v === 'number' ? v : null;
    };

    const hasOrder = list.some((p) => getOrder(p) !== null);
    return hasOrder
      ? [...(list as PageLike[])].sort((a, b) => (getOrder(a) ?? 1e9) - (getOrder(b) ?? 1e9))
      : (list as PageLike[]);
  }, [pages]);

  const featured = pageItems[0];
  const rightTop = pageItems[1];
  const rightBottom = pageItems[2];
  const bottomLeft = pageItems[3];
  const bottomMid = pageItems[4];

  // ====== CTA only if meaningful ======
  const primaryCTA = CTAs?.CTAs?.[0] ?? null;
  const ctaHref = primaryCTA ? toHref(primaryCTA) : null;
  const { target: ctaTarget, rel: ctaRel } = primaryCTA
    ? toTarget(primaryCTA)
    : { target: undefined, rel: undefined };

  const ctaTitle = (CTAs?.heading ?? '').trim();
  const ctaDesc = (CTAs?.sub_heading ?? '').trim();
  const ctaBtnText = (primaryCTA?.text ?? '').trim();

  const hasCtaCard = Boolean(
    ctaTitle || ctaDesc || (primaryCTA && ctaHref && ctaHref !== '#' && ctaBtnText)
  );

  const hasAny = Boolean(featured || rightTop || rightBottom || bottomLeft || bottomMid || hasCtaCard);

  // ====== Variants ======
  const sectionV: Variants = useMemo(
    () => ({
      hidden: {},
      show: reduce
        ? { transition: { staggerChildren: 0 } }
        : { transition: { staggerChildren: 0.14, delayChildren: 0.08 } },
    }),
    [reduce]
  );

  const titleV: Variants = useMemo(
    () => ({
      hidden: reduce
        ? { opacity: 1, y: 0, filter: 'blur(0px)' }
        : { opacity: 0, y: 18, filter: 'blur(12px)' },
      show: {
        opacity: 1,
        y: 0,
        filter: 'blur(0px)',
        transition: reduce
          ? { duration: 0.01 }
          : { type: 'spring', stiffness: 140, damping: 18, mass: 0.7 },
      },
    }),
    [reduce]
  );

  const underlineV: Variants = useMemo(
    () => ({
      hidden: reduce ? { scaleX: 1, opacity: 1 } : { scaleX: 0.15, opacity: 0 },
      show: {
        scaleX: 1,
        opacity: 1,
        transition: reduce ? { duration: 0.01 } : { duration: 0.7, ease: [0.22, 1, 0.36, 1] },
      },
    }),
    [reduce]
  );

  const gridV: Variants = useMemo(
    () => ({
      hidden: {},
      show: reduce
        ? { transition: { staggerChildren: 0 } }
        : { transition: { staggerChildren: 0.12, delayChildren: 0.12 } },
    }),
    [reduce]
  );

  const cardV: Variants = useMemo(
    () => ({
      hidden: reduce
        ? { opacity: 1, y: 0, scale: 1, filter: 'blur(0px)' }
        : { opacity: 0, y: 28, scale: 0.97, filter: 'blur(16px)' },
      show: {
        opacity: 1,
        y: 0,
        scale: 1,
        filter: 'blur(0px)',
        transition: reduce
          ? { duration: 0.01 }
          : { type: 'spring', stiffness: 120, damping: 18, mass: 0.8 },
      },
      hover: reduce
        ? {}
        : {
            y: -8,
            scale: 1.012,
            transition: { type: 'spring', stiffness: 340, damping: 22 },
          },
      tap: reduce ? {} : { scale: 0.988 },
    }),
    [reduce]
  );

  const arrowV: Variants = useMemo(
    () => ({
      hidden: { opacity: 0.9, x: 0 },
      hover: reduce
        ? {}
        : {
            opacity: 1,
            x: 8,
            transition: { type: 'spring', stiffness: 520, damping: 24 },
          },
    }),
    [reduce]
  );

  const iconWobbleV: Variants = useMemo(
    () => ({
      hidden: {},
      hover: reduce
        ? {}
        : {
            rotate: [0, -7, 7, -3, 0],
            scale: [1, 1.06, 1.03, 1.04, 1],
            transition: { duration: 0.6, ease: [0.22, 1, 0.36, 1] },
          },
    }),
    [reduce]
  );

  // ====== Featured mouse-parallax tilt ======
  const px = useMotionValue(0);
  const py = useMotionValue(0);
  const rotX = useTransform(py, [-0.5, 0.5], [6, -6]);
  const rotY = useTransform(px, [-0.5, 0.5], [-7, 7]);
  const hiX = useTransform(px, [-0.5, 0.5], [-22, 22]);
  const hiY = useTransform(py, [-0.5, 0.5], [-16, 16]);

  const IconBadge = ({
    icon: Icon,
    variant = 'light',
  }: {
    icon: React.ComponentType<{ className?: string }>;
    variant?: 'light' | 'dark';
  }) => {
    const base =
      variant === 'dark'
        ? 'bg-black/25 backdrop-blur-md ring-1 ring-white/15'
        : 'bg-white/55 backdrop-blur-md ring-1 ring-secondary/15';

    return (
      <MotionDiv
        variants={iconWobbleV}
        className={`inline-flex h-12 w-12 items-center justify-center rounded-2xl ${base}`}
      >
        <Icon className="h-5 w-5 text-muted" />
      </MotionDiv>
    );
  };

  const WhiteCard = ({
    page,
    icon,
  }: {
    page: PageLike;
    icon: React.ComponentType<{ className?: string }>;
  }) => {
    const cover = getPageCover(page);
    const desc = getPageSubheading(page);
    const shine = useCursorShine(shineEnabled);

    return (
      <MotionDiv variants={cardV} whileHover="hover" whileTap="tap" className="h-full">
        <Link
          href={`/${page.slug}`}
          onMouseMove={shine.onMove}
          onMouseEnter={shine.onEnter}
          onMouseLeave={shine.onLeave}
          className={[
            'group relative flex h-full flex-col overflow-hidden rounded-3xl p-8',
            'bg-white/75 backdrop-blur-xl',
            'ring-1 ring-secondary/15 shadow-[0_10px_30px_rgba(0,0,0,0.08)]',
            'transition-shadow hover:shadow-[0_18px_54px_rgba(0,0,0,0.14)]',
            'focus:outline-none focus-visible:ring-2 focus-visible:ring-muted/60',
          ].join(' ')}
        >
          {/* cover wash */}
          {cover ? (
            <>
              <div className="absolute inset-0">
                <Image
                  src={cover.src}
                  alt={cover.alt}
                  fill
                  sizes="(max-width: 1024px) 100vw, 33vw"
                  className="object-cover opacity-20"
                />
              </div>
              <div className="absolute inset-0 bg-white/82" />
            </>
          ) : null}

          {/* subtle grain */}
          <div className="pointer-events-none absolute inset-0 opacity-[0.10] [background-image:radial-gradient(rgba(0,0,0,0.25)_1px,transparent_1px)] [background-size:12px_12px]" />

          {/* ✅ cursor-follow shine */}
          <CursorShineLayer mx={shine.mx} my={shine.my} op={shine.op} variant="light" />

          {/* hover sheen */}
          <div className="pointer-events-none absolute inset-0 opacity-0 transition-opacity duration-300 group-hover:opacity-100">
            <div className="absolute -inset-40 rotate-12 bg-gradient-to-r from-transparent via-muted/12 to-transparent" />
          </div>

          <div className="relative z-10">
            <IconBadge icon={icon} variant="light" />

            <div className="mt-6">
              <div className="text-[18px] font-semibold tracking-tight text-lightblack">
                {getPageTitle(page)}
              </div>

              {desc ? (
                <p className={`mt-2 text-[13px] leading-relaxed text-secondary/80 ${clamp3}`}>{desc}</p>
              ) : null}
            </div>
          </div>

          <div className="relative z-10 mt-auto pt-6">
            <span className="inline-flex items-center gap-2 text-[12px] font-semibold tracking-wide text-lightblack/85">
              <span className="transition-colors group-hover:text-lightblack">Fedezze fel</span>
              <motion.span variants={arrowV} className="text-muted">
                →
              </motion.span>
            </span>
          </div>
        </Link>
      </MotionDiv>
    );
  };

  const FeaturedCard = ({ page }: { page: PageLike }) => {
    const cover = getPageCover(page);
    const desc = getPageSubheading(page);

    const shine = useCursorShine(shineEnabled);

    const onMove = (e: React.MouseEvent<HTMLElement>) => {
      if (reduce) return;
      // tilt
      const r = (e.currentTarget as HTMLElement).getBoundingClientRect();
      const nx = (e.clientX - r.left) / r.width - 0.5;
      const ny = (e.clientY - r.top) / r.height - 0.5;
      px.set(nx);
      py.set(ny);

      // shine
      shine.onMove(e);
    };

    const onLeave = () => {
      if (!reduce) {
        px.set(0);
        py.set(0);
      }
      shine.onLeave();
    };

    return (
      <MotionDiv
        variants={cardV}
        whileHover="hover"
        whileTap="tap"
        className="h-full"
        style={reduce ? undefined : ({ perspective: 980 } as any)}
      >
        <Link
          href={`/${page.slug}`}
          onMouseMove={onMove}
          onMouseEnter={shine.onEnter}
          onMouseLeave={onLeave}
          className={[
            'group relative block h-full overflow-hidden rounded-3xl',
            'ring-1 ring-black/5 shadow-[0_16px_60px_rgba(0,0,0,0.22)]',
            'min-h-[400px] sm:min-h-[460px] lg:min-h-0',
            'focus:outline-none focus-visible:ring-2 focus-visible:ring-muted/60',
          ].join(' ')}
        >
          <MotionDiv
            className="absolute inset-0"
            style={
              reduce
                ? undefined
                : ({
                    rotateX: rotX,
                    rotateY: rotY,
                    transformStyle: 'preserve-3d',
                  } as any)
            }
          >
            {cover ? (
              <div className="absolute inset-0">
                <Image
                  src={cover.src}
                  alt={cover.alt}
                  fill
                  sizes="(max-width: 1024px) 100vw, 66vw"
                  className="object-cover opacity-95"
                  priority={false}
                />
              </div>
            ) : null}

            {/* overlays (maradnak) */}
            <div className="absolute inset-0 bg-gradient-to-br from-black/10 via-black/35 to-black/75" />
            <MotionDiv className="absolute inset-0" style={reduce ? undefined : ({ x: hiX, y: hiY } as any)}>
              <div className="absolute inset-0 bg-[radial-gradient(900px_circle_at_30%_15%,rgba(255,255,255,0.16),transparent_55%)]" />
              <div className="absolute inset-0 bg-[radial-gradient(700px_circle_at_70%_25%,rgba(178,147,61,0.14),transparent_60%)]" />
            </MotionDiv>

            <div className="absolute inset-0 bg-black/35" />
            <div className="absolute inset-x-0 bottom-0 h-64 bg-gradient-to-t from-black/80 via-black/35 to-transparent" />
          </MotionDiv>

          {/* ✅ cursor-follow shine (dark variant) */}
          <CursorShineLayer mx={shine.mx} my={shine.my} op={shine.op} variant="dark" />

          {/* icon */}
          <div className="absolute left-6 top-6">
            <IconBadge icon={Scissors} variant="dark" />
          </div>

          {/* KIEMELT badge with continuous shine */}
          <div className="absolute right-6 top-6">
            <div className="relative overflow-hidden rounded-full bg-muted px-3 py-1 text-[10px] font-extrabold tracking-[0.18em] text-lightblack ring-1 ring-black/10">
              KIEMELT
              {!reduce ? (
                <motion.span
                  aria-hidden
                  className="pointer-events-none absolute inset-y-0 left-0 w-16 rotate-12 bg-gradient-to-r from-transparent via-white/32 to-transparent"
                  animate={{ x: ['-160%', '280%'] }}
                  transition={{ duration: 2.1, ease: 'linear', repeat: Infinity }}
                />
              ) : null}
            </div>
          </div>

          {/* content */}
          <div className="absolute inset-x-0 bottom-0 p-8 sm:p-10">
            <div className="max-w-2xl">
              <div className="text-[30px] font-semibold leading-[1.05] tracking-tight text-white sm:text-[38px]">
                {getPageTitle(page)}
              </div>

              {desc ? (
                <p className={`mt-3 text-[13px] leading-relaxed text-charcoal/90 ${clamp3}`}>{desc}</p>
              ) : null}

              <div className="mt-6">
                <span className="inline-flex items-center gap-2 text-[12px] font-semibold tracking-wide text-muted">
                  <span className="transition-opacity group-hover:opacity-95">Részletek Megtekintése</span>
                  <motion.span variants={arrowV} aria-hidden>
                    →
                  </motion.span>
                </span>
              </div>
            </div>
          </div>

          {/* sheen */}
          <div className="pointer-events-none absolute inset-0 opacity-0 transition-opacity duration-300 group-hover:opacity-100">
            <div className="absolute -inset-40 rotate-12 bg-gradient-to-r from-transparent via-white/14 to-transparent" />
          </div>
        </Link>
      </MotionDiv>
    );
  };

  const CTACard = () => {
    const shine = useCursorShine(shineEnabled);

    // muted glow on hover (inline style ok)
    const glowStyle = reduce
      ? undefined
      : ({
          boxShadow:
            '0 0 0 1px rgba(255,255,255,0.08), 0 18px 60px rgba(0,0,0,0.34), 0 0 50px rgba(178,147,61,0.20)',
        } as React.CSSProperties);

    return (
      <MotionDiv variants={cardV} whileHover="hover" whileTap="tap" className="h-full">
        <MotionDiv
          onMouseMove={shine.onMove}
          onMouseEnter={shine.onEnter}
          onMouseLeave={shine.onLeave}
          className={[
            'relative flex min-h-[210px] h-full flex-col items-center justify-center overflow-hidden rounded-3xl p-10 text-center',
            'bg-gradient-to-br from-lightblack via-lightblack to-black',
            'ring-1 ring-white/10 shadow-[0_18px_60px_rgba(0,0,0,0.32)]',
          ].join(' ')}
          style={glowStyle}
        >
          <div className="pointer-events-none absolute inset-0 bg-[radial-gradient(900px_circle_at_50%_25%,rgba(255,255,255,0.10),transparent_55%)]" />
          <div className="pointer-events-none absolute inset-0 bg-gradient-to-t from-black/25 via-transparent to-transparent" />

          {/* subtle grain */}
          <div className="pointer-events-none absolute inset-0 opacity-[0.10] [background-image:radial-gradient(rgba(255,255,255,0.18)_1px,transparent_1px)] [background-size:12px_12px]" />

          {/* ✅ cursor-follow shine */}
          <CursorShineLayer mx={shine.mx} my={shine.my} op={shine.op} variant="dark" />

          <div className="relative">
            <MotionDiv
              variants={iconWobbleV}
              className="mx-auto inline-flex h-12 w-12 items-center justify-center rounded-2xl bg-white/10 ring-1 ring-white/10"
            >
              <Calendar className="h-5 w-5 text-white" />
            </MotionDiv>

            {ctaTitle ? (
              <div className="mt-6 text-[22px] font-semibold tracking-tight text-white">{ctaTitle}</div>
            ) : null}

            {ctaDesc ? (
              <p className={`mx-auto mt-2 max-w-xs text-[13px] leading-relaxed text-charcoal/80 ${clamp3}`}>
                {ctaDesc}
              </p>
            ) : null}

            {primaryCTA && ctaHref && ctaHref !== '#' && ctaBtnText ? (
              <div className="mt-7 flex justify-center">
                <motion.div
                  whileHover={reduce ? undefined : { y: -2 }}
                  whileTap={reduce ? undefined : { scale: 0.98 }}
                  transition={reduce ? undefined : { type: 'spring', stiffness: 520, damping: 28 }}
                >
                  <Button
                    as="a"
                    href={ctaHref}
                    target={ctaTarget}
                    rel={ctaRel}
                    variant="primary"
                    className="h-11 rounded-full bg-muted px-8 text-[12px] font-extrabold tracking-wide text-lightblack shadow-sm hover:bg-muted/90"
                  >
                    {ctaBtnText}
                  </Button>
                </motion.div>
              </div>
            ) : null}
          </div>
        </MotionDiv>
      </MotionDiv>
    );
  };

  return (
    <section ref={sectionRef} className="relative bg-charcoal">
      {/* Premium 2026 backdrop */}
      <motion.div aria-hidden className="pointer-events-none absolute inset-0" style={{ opacity: bgO }}>
        <motion.div className="absolute inset-0" style={{ y: bgY1 }}>
          <div className="absolute inset-0 bg-[radial-gradient(1200px_circle_at_20%_10%,rgba(178,147,61,0.14),transparent_55%)]" />
        </motion.div>

        <motion.div className="absolute inset-0" style={{ y: bgY2 }}>
          <div className="absolute inset-0 bg-[radial-gradient(980px_circle_at_88%_70%,rgba(41,38,36,0.10),transparent_60%)]" />
        </motion.div>

        {!reduce ? (
          <motion.div
            className="absolute inset-0"
            animate={{ x: [0, 14, 0], y: [0, -10, 0] }}
            transition={{ duration: 14, ease: [0.22, 1, 0.36, 1], repeat: Infinity }}
          >
            <div className="absolute inset-0 bg-[radial-gradient(760px_circle_at_55%_30%,rgba(178,147,61,0.10),transparent_62%)]" />
          </motion.div>
        ) : null}

        <div className="absolute inset-0 opacity-[0.08] [background-image:radial-gradient(rgba(0,0,0,0.35)_1px,transparent_1px)] [background-size:14px_14px]" />
      </motion.div>

      <motion.div
        variants={sectionV}
        initial="hidden"
        whileInView="show"
        viewport={{ once: true, amount: 0.25 }}
        className="relative"
      >
        <div className="mx-auto max-w-6xl px-4 pb-24 pt-20 sm:px-6 lg:px-8">
          {/* Title */}
          <motion.div variants={titleV} className="text-center">
            {!!heading && <Heading className="text-center text-lightblack">{heading}</Heading>}

            <motion.div
              variants={underlineV}
              className="mx-auto mt-5 h-[2px] w-24 origin-left rounded-full bg-muted/80"
            />

            {!!sub_heading && (
              <Subheading className="mx-auto mt-6 max-w-2xl text-center text-secondary">
                {sub_heading}
              </Subheading>
            )}
          </motion.div>

          {/* Bento grid */}
          {hasAny ? (
            <motion.div variants={gridV} className="mt-14 space-y-2">
              {/* Top row */}
              <div className="grid grid-cols-1 gap-2 lg:grid-cols-3 lg:items-stretch">
                {featured ? (
                  <div className="lg:col-span-2 h-full">
                    <FeaturedCard page={featured} />
                  </div>
                ) : null}

                {rightTop || rightBottom ? (
                  <div className={(featured ? 'lg:col-span-1' : 'lg:col-span-3') + ' h-full'}>
                    <div className="grid h-full grid-cols-1 gap-2 lg:grid-rows-2">
                      {rightTop ? <WhiteCard page={rightTop} icon={Sparkles} /> : null}
                      {rightBottom ? <WhiteCard page={rightBottom} icon={Gem} /> : null}
                    </div>
                  </div>
                ) : null}
              </div>

              {/* Bottom row */}
              <div className="grid grid-cols-1 gap-2 lg:grid-cols-3">
                {bottomLeft ? <WhiteCard page={bottomLeft} icon={Scissors} /> : null}
                {bottomMid ? <WhiteCard page={bottomMid} icon={User} /> : null}
                {hasCtaCard ? <CTACard /> : null}
              </div>
            </motion.div>
          ) : null}
        </div>
      </motion.div>
    </section>
  );
};
